.gallery {
  padding-top: 60px;

  @media screen and (min-width: $tablet) {
    padding-top: 100px;
  }
}

.gallery-container {
  padding-left: 0;
  padding-right: 0;

  @media screen and (min-width:$tablet) {
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and (min-width:$desktop) {
    padding-left: 46px;
    padding-right: 46px;
  }
}

.gallery-title {
  text-align: left;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 40px;
  line-height: calc(60/40);
  color: $main-font-color;
  padding-left: 20px;

  @media screen and (min-width:$tablet) {
    margin-bottom: 24px;
    padding-left: 0;

  }

  @media screen and (min-width:$desktop) {
    margin-bottom: 20px;
    padding-left: 20px;

  }

}